.dc-gallery
    padding: 30px 0 0
    .tab-news
        padding-top: 50px
        margin-bottom: 30px
        ul
            padding-left: 0
            display: flex
            align-items: center
            justify-content: center
            li
                width: 270px
                margin: 0 20px
                list-style: none
                padding: 10px 8px
                border: 1px solid #005cb5
                border-radius: 30px
                text-align: center
                a
                    color: #005cb5
                    font-weight: 500
                    font-size: 16px
                &:hover,&.active
                    background: #005cb5
                    a
                        color: #fff
    .main-gallery-image
        
        .big, .small
            .item
                margin-bottom: 20px
                position: relative
                overflow: hidden
                a
                    color: #fff
                figure
                    position: relative
                    display: flex
                    justify-content: center
                    align-items: center
                    .button-search 
                        position: absolute
                        top: 20px
                        right: 20px
                        width: 40px
                        height: 40px
                        background: #fff
                        z-index: 9
                        display: flex
                        justify-content: center
                        align-items: center
                        opacity: 0
                        em
                            color: #005cb5
                            font-size: 24px
                    .image
                        width: 100%
                        img
                            width: 100%
                    figcaption
                        position: absolute
                        z-index: 2
                        transition: 0.5s all ease-in-out
                        opacity: 1
                        bottom: 0
                        h3
                            color: #fff
                            font-size: 16px
                            font-weight: 500
                    .play
                        position: absolute
                        display: flex
                        justify-content: center
                        align-items: center
                        z-index: 99
                        width: 100%
                        height: 100%
                        bottom: 0
                        figcaption
                            bottom: 5%
                            
                    &::after
                        content: ''
                        width: calc(100% - 40px)
                        height: calc(100% - 40px)
                        position: absolute
                        background: #005cb5
                        z-index: 1
                        transition: 0.5s all ease-in-out
                        opacity: 0
                    &:hover
                        .button-search 
                            opacity: 1
                        figcaption
                            opacity: 1
                            bottom: 45%
                            h3
                                border-bottom: 1px solid #fff
                        &::after
                            opacity: .8
                        .play
                            figcaption
                                bottom: 20%
                                h3
                                    border-bottom: 1px solid #fff
        .big
            .item
                figure
                    &:hover
                        .play
                            figcaption
                                bottom: 35%

@media(min-width: 768px)
    .dc-gallery
        padding: 30px 0
        .main-gallery-image
            .big
                .item
                    margin-bottom: 0
                    figure
                        .image
                            height: 490px
                            width: 100%
                            img
                                height: 490px
                                width: 100%
            .small
                .item
                    margin-bottom: 30px
                    figure
                        .image
                            height: 230px
                            width: 100%
                            img
                                height: 230px
                                width: 100%
                            
