.dc-home-3
    // background: url('../img/banner/2.png')
    background: url('/Data/Sites/1/media/banner/2.png')
    background-position: center center
    background-size: cover
    .main-service
        .content
            display: flex
            justify-content: center
            padding-top: 60px
            h3
                width: 900px
                text-align: center
                color: #ffffff
                font-size: 16px
                font-weight: 400
        .button-service
            display: flex
            justify-content: center
            margin-top: 30px
            margin-bottom: 150px
            a
                display: flex
                align-items: center
                background: #005cb5
                padding: 5px 15px
                border-radius: 30px
                color: #ffffff
                text-transform: uppercase
                em 
                    padding-right: 10px
                    padding-left: 10px
                    font-size: 22px
                p
                    padding-right: 10px
