.wrap-form
    margin-top: 30px
    @media(min-width: 1025px)
        margin-top: 0
    input[type='text'], textarea , select, .riTextBox 
        width: 100%
        padding: 12px 20px
        font-size: 15px
        background: #f8f8f8
        border: 1px solid #e9e9e9
        color: #333333
        caret-color: #005cb5
        &:placeholder
            color: #999999
        &:focus
            outline: none
            border-color: #005cb5
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3)
    textarea
        height: 180px
    &>div
        display: flex
        flex-wrap: wrap
        margin: 0 -5px
        .qtext, .qdropdown, .qdate
            padding: 0 5px
            flex: 0 0 50% !important
            margin-bottom: 20px
            @media(max-width: 768px)
                flex: 0 0 100%
        .qparagraph
            flex: 0 0 100%
            padding: 0 5px
            margin-bottom: 20px
        span
            color: red
            font-size: 13px
    .frm-captcha
        display: flex
        align-items: center
        flex-direction: row-reverse
        justify-content: flex-end
        display: flex
        .frm-captcha-input
            width: 100%
            label
                display: none
            input
                width: 100%
        .RadCaptcha
            width: auto!important
            position: relative
            span
                position: absolute
                bottom: 100%
            &>div>div
                align-items: center
                display: flex
                width: 227px
        .rcRefreshImage
            margin: 0 10px
            font-size: 0
            &:after
                content: url('/Data/Sites/1/media/change.png')
    .frm-btnwrap
        display: flex
        align-items: center
        margin-top: 20px
        height: 100%
        label
            display: none
        .frm-btn-reset
            display: none
        .frm-btn
            width: 100%
        input
            width: 100%
            padding: 5px 30px
            background: #005cb5
            text-transform: uppercase
            color: #fff
            border: none
            border-radius: 30px
            
    @media(min-width: 768px)
        .frm-captcha
            width: 75%
            float: left
        .frm-btnwrap
            float: left
            margin-top: 0
            width: 25%
            margin-left: 10px
    @media(min-width: 1025px)
        .frm-btnwrap
            input
                transform: translateY(5px)
        .frm-captcha
            .frm-captcha-input
                width: calc(100% - 227px)
                input
                    width: 80%

        