header
    position: fixed
    width: 100%
    background: #ffffff
    z-index: 99
    top: 0
    left: 0
    border-bottom: 2px solid #005cb5
    padding-bottom: 10px
    .backdrop
        position: fixed
        left: 0
        top: 0
        opacity: 0
        pointer-events: none
        width: 100%
        height: 100vh
        background: rgba(0,0,0,.8)
        z-index: 998
        &.active
            opacity: 1
            pointer-events: initial
    .main-header
        display: flex
        justify-content: space-between
        align-items: center
        .logo
            padding-bottom: 1px
            top: 15px
            a
                position: relative
                top: 10px
                &:before
                    // content: url(../img/logo-after.png)
                    content: url(/Data/Sites/1/media/logo-after.png)
                    position: absolute
                    top: calc(100% - -22px)
                    left: -53px
                    z-index: 100
                // transform: translateX(-30%)
            // img
            //     width: 50px
        .header-top
            display: flex
            align-items: center
            justify-content: flex-end
            margin: 5px 0 10px
            .ModuleContent
                display: flex
                justify-content: center
                align-items: center
            .language
                ul
                    display: flex
                    padding-left: 0
                    li
                        list-style: none
                        padding: 5px 20px 5px 0
                        a
                            h3
                                font-size: 14px
                                font-weight: 400
                                color: #005cb5
                        &.active
                            color: #005cb5
            .contact
                display: flex
                span
                    padding-right: 25px
                    padding-left: 5px
                em,span
                    color: #005cb5
                    font-weight: 600
                    font-size: 14px
            .button-search
                display: inline
                em
                    padding: 5px 25px 5px 0
                    color: #005cb5
                    font-size: 18px
            
        .header-bottom
            margin-bottom: 10px
            .list-item
                display: flex
                padding-left: 0
                li
                    list-style: none
                    padding: 0 15px
                    text-transform: uppercase
                    position: relative
                    a
                        color: #333333
                        font-weight: 500
                        font-size: 14px
                    &.active,&:hover
                        a
                            color: #005cb5
                    &:before
                        content: ''
                        position: absolute
                        width: 1px
                        height: 100%
                        background: #e5e5e5
                        top: 0
                        right: 0
                        transform: translateX(-50%)
                    &:last-child
                        &:before
                            display: none
            
    .mobile-wrapper
        .heading
            .close-mobile-wrapper
                display: none
        .searchbox
            input
                padding: 5px
                border: none
    
    .icon-link
        position: fixed
        padding-top: 30px
        right: 1%
        ul
            li
                list-style: none
                padding: 5px
    .search-wrapper
        display: none
    @media(max-width: 1025px)
        padding-bottom: 0
        .main-header
            .logo
                padding: 10px 0
        .mobile-wrapper
            position: fixed
            width: 100%
            height: 100vh
            background: linear-gradient(125deg,#005cb5 ,#0096d7)
            top: 72px
            right: -100%
            display: flex
            justify-content: flex-start
            transition: all 1s cubic-bezier(.22,.44,0,1)
            align-items: center
            flex-direction: column
            z-index: 199
            
            &.active
                right: 0
            .heading
                .close-mobile-wrapper
                    display: flex
                    em
                        top: 10px
                        right: 10px
                        position: absolute
                        font-size: 30px
                        color: #fff
            .list-item
                padding: 50px 30px 40px
                border-radius: 3px
                text-align: center
                li
                    margin-bottom: 10px
                    list-style: none
                    font-size: 14px
                    text-transform: uppercase
                    font-weight: 500
                    color: #fff
                    a
                        &:hover
                            color: #333333
            .contact
                display: flex
                color: #ffffff
                align-items: center
                justify-content: center
                .phone
                    padding-right: 25px
                span
                    padding-left: 5px
                em,span
                    font-weight: 500
                    font-size: 14px
            
            .language
                margin-top: 20px
                ul
                    padding-left: 0
                    display: flex
                    justify-content: space-between
                    align-items: center
                    
                    li
                        list-style: none
                        color: #fff
                        padding: 0 20px
                        h3 
                            font-weight: 500
                            font-size: 18px
            
        .main-header
            .logo
                img
                    max-width: 200px
                a
                    top: 0
                    &:before
                        display: none
            .header-bottom
                .button-toggle
                    display: flex
                    font-size: 30px
                    color: #005cb5
            .header-top
                .button-search
                    display: none
                
        .toggle-button
            order: 1
            width: 2rem
            height: 2rem
            display: flex
            margin-left: 20px
            justify-content: center
            align-items: center
            .button
                height: 2px
                width: 30px
                background: linear-gradient(125deg,#0096d7,#005cb5)
                position: relative
                transition: all 0ms 300ms
                &:after
                    content: ""
                    position: absolute
                    left: 0
                    width: 100%
                    height: 100%
                    bottom: 8px
                    background: linear-gradient(125deg,#0096d7,#005cb5)
                    transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1)
                &:before
                    content: ""
                    position: absolute
                    left: 0
                    width: 100%
                    height: 100%
                    top: 8px
                    background: linear-gradient(125deg,#0096d7,#005cb5)
                    transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1)
            &.active
                .button
                    background: transparent
                    &:before
                        top: 0
                        transform: rotate(45deg)
                        transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1)
                    &:after
                        bottom: 0
                        transform: rotate(-45deg)
                        transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1)