.dc-dichvu-2
    padding-bottom: 60px
    .main-title
        margin-top: 50px
    .swiper-container
        .swiper-slide
            .image
                img
                    width: 100%
            .title
                position: relative
                bottom: 30px
                z-index: 9
                display: flex
                flex-direction: column
                justify-content: center
                background: #f5f4ed
                transform: translateY(-30px)
                width: 100%
                
                h3
                    display: flex
                    align-items: center
                    color: #000000
                    padding: 10px 20px 
                    font-size: 18px
                    justify-content: center
                    height: 50px
                    width: auto
                    text-transform: uppercase
                    text-align: center
                .star    
                    em
                        color: #ffcc00
                        padding-left: 20px
                .note,.address,.car
                    display: flex
                    justify-items: center
                    align-items: center
                    padding-left: 20px
                    em
                        color: #005cb5
                        margin-right: 10px
                    h5
                        color: #999999
                        font-size: 14px
                    span
                        margin-left: 5px
                        color: #333333
                        font-size: 14px
                .car
                    margin-bottom: 10px
                .info-price
                    padding: 10px 20px
                    display: flex
                    justify-content: space-between
                    border-top: 1px solid #d1d1d1
                    .price,.sale
                        p
                            color: #005cb5
                            font-size: 12px
                    .price
                        span
                            color: #989898
                            font-weight: 500
                            font-size: 14px
                            text-decoration: line-through
                    .sale
                        text-align: left
                        span 
                            color: #005cb5
                            font-weight: 600
                            font-size: 14px
            
                .button-small
                    display: flex
                    justify-content: space-between
                    align-items: center
                    padding: 0 30px 20px
                    background: #f5f4ed
                    .book
                        display: flex
                        align-items: center
                        width: 150px
                        a
                            padding: 10px 15px
                            background: #005cb5
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            width: 100%
                            text-align: center
                    .hotline
                        display: flex
                        align-items: center
                        width: 150px
                        a
                            width: 100%
                            display: flex
                            padding: 10px 20px
                            background: #f78915
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            em
                                padding-right: 5px
@media(min-width: 1025px)
    .dc-dichvu-2
        .swiper-container
            padding-top: 100px
            
            .swiper-slide
                .title
                    justify-content: center
                    width: 370px
                    left: 50%
                    top: -50px
                    transform: translateX(-50%)
                    
                    h3
                        width: 370px
                    .star
                        em
                            padding-left: 0
                            &.fisrt
                                padding-left: 20px
                    