.dc-thuexe-ct-2
    padding: 60px 0 30px 0
    .swiper-container
        position: relative
        .button
            position: absolute
            display: flex
            justify-content: space-between 
            align-items: center
            width: 100%
            left: 0
            top: 0                
            height: 100%
            font-size: 26px
            color: #999999
            z-index: 99
            pointer-events: none
            .left, .right
                width: 35px
                height: 35px
                font-size: 40px
                display: flex
                justify-content: center
                align-items: center
                
                pointer-events: auto
    .main-title
        margin-bottom: 30px
    .swiper-slide
        .image
            background: #f3f3f3
            img
                width: 100%
                object-fit: scale-down
        figcaption
            padding: 15px 0 0
            position: relative
            bottom: 30px
            z-index: 99
            display: flex
            flex-direction: column
            justify-content: center
            background: #fff
            transform: translateY(15px)
            width: auto
            box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
            text-align: center
            border-bottom: 2px solid #005cb5
            h3
                text-transform: uppercase
                font-size: 16px
                color: #333333
            p
                color: 14px 
                color: #333333
                span
                    color: #666666
                    padding-left: 10px
            .button-detail
                display: flex
                justify-content: center
                align-content: center
                width: 100%
                margin-top: 10px
                border-top: 1px solid #d7d7d7
                .detail,.book-car
                    flex: 0 0 50%
                    padding: 10px 0
                    a
                        display: flex
                        justify-content: center
                        align-items: center
                        em
                            padding-right: 10px
                        p
                            color: #000000
                            font-size: 14px
                            text-transform: uppercase
                    &:hover
                        background: #005cb5
                        a
                            color: #fff
                            p,em   
                                color: #fff
                .detail
                    border-right: 1px solid #d7d7d7
    @media(min-width: 1025px)
        .swiper-slide
            .image
                height: 400px
                img
                    height: 100%
            figcaption
                justify-content: center
                width: 370px
                left: 50%
                top: -50px
                transform: translateX(-50%)    