.dc-ds-tuyendung
    .table-responsive 
        white-space: nowrap
        overflow-x: auto
        padding: 40px 0
        .table 
            width: 100%
            overflow: hidden
            box-shadow: 0 0 0 1px #e1e1e1 inset
            thead
                background: #005cb5
                tr
                    th
                        border-top: none!important
                        padding: 20px 30px
                        text-align: left
                        color: #ffffff
                        font-size: 16px
                        font-weight: 500
                        border-bottom: 1px solid #e1e1e1
                        text-transform: uppercase
                    .noidung
                        text-align: left
            tbody 
                tr
                    border-bottom: 1px solid #ebebeb
                    td 
                        transition: all .4s ease-in-out
                        padding: 20px 30px
                        text-align: center
                        
                        a 
                            
                            transition: all .4s ease-in-out
                            color: #333333
                        &:last-child
                            a
                                color: #005cb5
                                border-bottom: 1px solid #0095da
                        &:first-child
                            text-align: left
                    
                        
                    .noidung 
                        text-align: left
                        a
                            border-bottom: none
                    &:hover 
                        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3)
                        transition: all .4s ease-in-out
                        td 
                            color: #0095da
                            a 
                                color: #0095da
                            &:last-child
                                a
                                    border-bottom: 1px solid #0095da
                                    
                        .noidung 
                        
                            a
                                border-bottom: none