.dc-binh-luan
    background: #f0f4f7
    padding: 30px 0
    .main-comment
        position: relative
        .swiper-slide
            
            figure
               
                .image
                    position: relative  
                    width: 80px
                    height: 80px
                    margin-left: 20px
                    transform: translateY(50%)
                    img
                        width: 100%
                        height: 100%
                        border-radius: 50%
                figcaption
                    background: #fff
                    padding: 50px 20px 20px
                    .name
                        h3
                            color: #333333
                            font-size: 18px
                        .comment
                            p
                                font-size: 12px
                                color: #666666
                                overflow: hidden
                                text-overflow: ellipsis
                                line-height: 25px
                                -webkit-line-clamp: 4
                                height: 100px
                                display: -webkit-box
                                -webkit-box-orient: vertical
                    .star
                        em
                            color: #fb8f29

        .swiper-butoon
            display: flex
            .swiper-prev
                position: absolute
                top: 60%
                right: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 30px
            .swiper-next
                position: absolute
                top: 60%
                left: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 30px
@media(min-width: 768px)
    .dc-binh-luan
        padding: 60px 0
        .main-title
            margin-bottom: 20px
        .main-comment
            .swiper-butoon
                .swiper-prev
                    right: -40px
                    em
                        font-size: 50px
                .swiper-next
                    left: -40px
                    em
                        font-size: 50px
                