.dc-dulich-ct
    .post
        .title-posts
            h2
                color: #005cb5
                font-size: 24px
                font-weight: 600
                margin-bottom: 25px
        .date
            span
                color: #989898
                font-size: 12px
        .share
            padding-top: 40px
            display: flex
            justify-content: flex-start
            align-items: center
            .zalo
                width: 48px
                height: 22px
                background: #01a5fa
                transform: translateY(2px)
                display: flex
                justify-content: center
                align-items: center
                a
                    
                    
                    .image
                        width: 15px
                        img
                            width: 100%
                            height: 100%
            a
                em
                    width: 50px
                    height: 21px
                    display: flex
                    justify-content: center
                    align-items: center
                    font-size: 12px
                    color: #fff
                    margin-top: 2px   
                    background: #a3b4bb
            #share
                
                .jssocials-shares
                    display: flex
                    // 
                    .jssocials-share-link
                        padding: 5px 20px
                        margin-right: 5px
                        font-size: 10px
        .full-content
            .date
                color: #989898
                font-size: 12px
            p
                padding: 10px 0
                color: #333333
                font-size: 16px
                img
                    width: 100%
    .list-post
        padding-top: 30px
        background: #f3f3f3
        h3
            padding: 10px 0
            color: rgba(255, 255, 255, 255)
            text-transform: uppercase
            font-size: 18px
            background: #005cb5
            text-align: center
            margin-bottom: 20px
        .item
            box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
            margin-bottom: 20px
            &:first-child
                padding-top: 0
            a
                color: #333333
            figure
                .image
                    height: 115px
                    width: 100%
                    img
                        height: 100%
                        width: 100%
                        object-fit: cover
            figcaption
                padding-top: 20px
                .date
                    color: #989898
                    font-size: 12px
                .title
                    padding-bottom: 10px
                    h4
                        color: #333
                        font-size: 14px
                        overflow: hidden
                        text-overflow: ellipsis
                        line-height: 25px
                        -webkit-line-clamp: 2
                        height: 50px
                        display: -webkit-box
                        -webkit-box-orient: vertical
            &:hover
                figcaption
                    .title
                        h4
                            color: #005cb5
                            text-decoration: underline
    table
        width: 100%
        td 
            padding: 5px 10px
            font-size: 18px
            font-weight: 400
            color: #333333
            &:first-child
                font-size: 18px
                font-weight: 600
                color: #333333
                width: 180px
    .link 
        margin-top: 20px
        display: flex
        align-items: center
        a 
            padding: 10px 20px
            background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 100%) !important
            color: #333333
            font-size: 14px
            font-weight: 500
            border: 1px solid #005cb5
            transition: .4s ease-in-out all
            text-transform: uppercase
            margin-right: 25px
            &:hover
                background: #005cb5 !important
                color: #ffffff
            &:last-child
                margin-right: 0
        p 
            font-size: 16px
            font-weight: 400
            color: #333333
            margin-right: 25px
.dc-dulich-ct
    .list-post 
        background: none
        h3 
            margin-bottom: 20px
        .item
           
            transition: .4s ease-in-out all
            &:hover
                background: #f9f9f9
                cursor: pointer
        .date
            date
                font-size: 12px
                font-weight: 400
                color: #989898
        .title
            h4 
                font-size: 14px
                font-weight: 500
                color: #333333
.pages.newsdetailpager
    .modulepager
        .pagination
            display: none
@media(min-width: 1025px)
    .dc-dulich-ct
        padding: 50px 0
        .post
            .title-posts
                h2
                    font-size: 30px
        .list-post
            padding-top: 0
            .item
                margin-bottom: 20px
            h3
                font-size: 20px
        .full-content
            p
                font-size: 18px