.dc-dichvu
    .tab-news
        padding-top: 50px
        margin-bottom: 30px
        ul
            padding-left: 0
            display: flex
            align-items: center
            justify-content: center
            overflow: auto
            white-space: nowrap
            li
                width: 270px
                margin: 0 20px
                list-style: none
                padding: 10px 8px
                border: 1px solid #005cb5
                border-radius: 30px
                text-align: center
                a
                    color: #005cb5
                    font-weight: 600
                    font-size: 16px
                    text-transform: uppercase
                &:hover,&.active
                    background: #005cb5
                    a
                        color: #fff
    .list-item
        figure
            a
                .image
                    img
                        width: 100%
                figcaption   
                    position: relative
                    bottom: 30px
                    z-index: 9
                    display: flex
                    justify-content: center
                    
                    .title
                        background: #ffffff
                        border-bottom: 2px solid #005cb5
                        padding-bottom: 10px
                        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
                        h3
                            display: flex
                            align-items: center
                            color: #000000
                            padding: 10px 20px 
                            font-size: 14px
                            justify-content: center
                            height: 50px
                            width: 300px
                            text-transform: uppercase
                            text-align: center
                            border-bottom: 1px solid #cccccc
                            margin-bottom: 10px
                        p
                            text-align: center
                            color: #333333
                            &.price
                                
                                padding-bottom: 10px
                                font-weight: 500
                            span
                                padding-left: 5px
                                color: #666666 
                                font-size: 14px
                    &:hover
                        .title
                            background: #005cb5
                            h3,p,span
                                color: #fff
@media(min-width: 1025px)
    .dc-dichvu
        .list-item
            figure
                a
                    .image
                        height: 400px
                        img
                            height: 100%
                    figcaption
                        .title
                            h3
                                width: 370px