.slide-home
    .main-slide
        .swiper-slide
            position: relative
            a
            img
                width: 100%
                max-height: 100%
        .main-title
            position: absolute  
            bottom: 5%
            z-index: 9  
            text-align: center
            width: 100%
            h1
                font-size: 16px
                font-weight: 400
                color: #fff
                text-transform: uppercase
            .button
                cursor: pointer
                em
                    color: #ffffff
                    font-size: 24px
    .main-item
        padding-top: 60px
        figure
            text-align: center
    @media(min-width: 768px)
        .main-slide
            .main-title
                bottom: 40%
                h1
                    font-size: 24px
    @media(min-width: 1025px)
        .main-slide
            .main-title
                text-align: center
                h1
                    padding: 0 100px
                    font-size: 36px
                    font-weight: 300
                