.breadcrumb-section
    background: rgba(#000, .5)
    display: none
    transform: translateY(-42px)
    position: relative
    z-index: 9
    
    ol.breadcrumb
        display: flex
        padding: 10px 0
        align-items: center
        list-style: none
        padding-left: 0
        
        
        li
            display: flex
            align-items: center
            position: relative
            a
                color: #fff
                font-size: 14px
                &.active
                    color: #fff
            + li
                &:before
                    content: '\f142'
                    font: normal normal normal 24px/1 Material Design Icons
                    font-size: 13px
                    padding: 0 5px
                    color: #fff
            &:first-child
                a
                    font-size: 0
                &:before
                    content: '\f2dc'
                    font: normal normal normal 14px/1 Material Design Icons
                    color: #fff
                    position: absolute
                    top: 50%
                    left: -10px
                    transform: translateY(-50%)
    @media(min-width: 768px)
        display: block