.dao-cham-td-ct
    .dao-cham-title
        margin-bottom: 15px
        h1 
            font-size: 20px
            font-weight: 700
            color: #005cb5
            @media(min-width: 768px)
                font-size: 22px
            @media(min-width: 1025px)
                font-size: 24px
    .main-attr
        margin-bottom: 20px
        h3 
            font-size: 18px
            font-weight: 700
            color: #333333
        .title
            margin-bottom: 10px
        .content
            .item
                display: flex
                margin-bottom: 10px
            .left
                min-width: 150px
                @media(min-width: 1025px)
                    min-width: 210px
                h6 
                    font-size: 16px
                    font-weight: 700
            .right
                font-size: 16px
                font-weight: 400
                color: #666666
    .full-content
        font-size: 16px
        font-weight: 400
        color: #333333
        h4
            font-size: 18px
            font-weight: 700
            margin-bottom: 15px
        ul 
            margin: 0
            padding: 0
            margin-bottom: 20px
            li 
                margin-left: 20px
                position: relative
                padding: 5px 0
                list-style: none
                display: flex
                align-items: center
                &:before
                    content: ''
                    position: absolute
                    top: 50%
                    left: -20px
                    background: #999999
                    width: 8px
                    height: 8px
                    border-radius: 50%
                    transform: translateY(-50%)
    .main-button
        margin-top: 25px
        .ung-tuyen, .gui-hs
            a 
                font-size: 14px
                font-weight: 700
                color: #005cb5
                text-transform: uppercase
                padding: 8px 50px
                border: 1px solid #005cb5
                background: #ffffff
                border-radius: 18px
                transition: .4s ease-in-out all
        .ung-tuyen
            margin-bottom: 15px
        .or
            font-size: 16px
            font-weight: 400
            margin-bottom: 15px
        .gui-hs
            margin-bottom: 25px
        @media(min-width: 768px)
            display: flex
            align-items: center
            margin-bottom: 25px
            .ung-tuyen, .gui-hs, .or
                margin-bottom: 0
            .or
                margin: 0 20px
        @media(min-width: 1025px)
            .ung-tuyen, .gui-hs
                a 
                    &:hover
                        color: #ffffff
                        background: #005cb5
    .title-other
        padding: 10px 30px
        background: #005cb5
        h2 
            font-size: 20px
            font-weight: 700
            color: #ffffff
    .main-item
        .item
            background: #f3f3f3
            padding: 15px 30px
            border-bottom: 1px solid #e3e3e3
        .date 
            time 
                font-size: 12px
                color: #989898
        h3 
            font-size: 14px 
            color: #666666