.Module-281
    display: none
    padding: 20px
    justify-content: center
    align-items: center
    position: relative
    text-align: center
    position: fixed
    z-index: 999
    transition: .5s all ease-in-out
    &.active
        display: flex
    .searchbox
        display: flex
        // justify-content: center
        align-items: center
        width: 70%
        left: 50%
        top: 15%
        position: fixed
        transform: translateX(-50%)
        background: #000000
        border-radius: 5px
        input
            display: flex
            padding: 8px 20px
            border: 0
            background: #000000
            color: #fff
            &:focus
                outline: none
            width: 100%
        button
            background: none
            border: 0
            &:focus
                outline: none
            &:hover
                em
                    color: #cb7312
            em
                display: flex
                color: #fff
                right: 0
                font-size: 25px
