.dc-home-5
    background: #f0f4f7
    .main-item
        padding-top: 40px
        figure
            display: flex
            flex-direction: column
            justify-content: center
            align-items: center
            margin-bottom: 20px
            .image
                margin-bottom: 10px
                height: 70px
                width: 70px
                img
                    height: 100%
                    width: 100%
                    object-fit: none
            figcaption
                padding: 0 30px
                p
                    height: 50px
                    text-align: center
                    font-size: 14px
    @media(min-width: 1025px)
        .main-item
            figure
                margin-bottom: 0px
