.dc-dichvu-thuexe
    .tab-news
        padding-top: 50px
        margin-bottom: 30px
        ul
            padding-left: 0
            display: flex
            align-items: center
            justify-content: center
            li
                width: 270px
                margin: 0 20px
                list-style: none
                padding: 10px 8px
                border: 1px solid #005cb5
                border-radius: 30px
                text-align: center
                a
                    color: #005cb5
                    font-weight: 600
                    font-size: 16px
                &:hover,&.active
                    background: #005cb5
                    a
                        color: #fff
       
    .main-service
        .description
            p
                padding: 20px 50px
                font-size: 14px
                color: #333333
                text-align: center
                span
                    color: #005cb5
        .image
            background: #f3f3f3
            img
                width: 100%
                object-fit: scale-down
        figcaption
            padding: 15px 0 0
            position: relative
            bottom: 30px
            z-index: 99
            display: flex
            flex-direction: column
            justify-content: center
            background: #fff
            transform: translateY(15px)
            width: auto
            box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
            text-align: center
            border-bottom: 2px solid #005cb5
            h3
                text-transform: uppercase
                font-size: 16px
                color: #333333
            p
                color: 14px 
                color: #333333
                span
                    color: #666666
                    padding-left: 10px
            .button-detail
                display: flex
                justify-content: center
                align-content: center
                width: 100%
                margin-top: 10px
                border-top: 1px solid #d7d7d7
                .detail,.book-car
                    flex: 0 0 50%
                    padding: 10px 0
                    a
                        display: flex
                        justify-content: center
                        align-items: center
                        em
                            padding-right: 10px
                        p
                            color: #000000
                            font-size: 14px
                            text-transform: uppercase
                    &:hover
                        background: #005cb5
                        a
                            color: #fff
                            p,em   
                                color: #fff
                .detail
                    border-right: 1px solid #d7d7d7
                        
@media(min-width: 1025px)
    .dc-dichvu-thuexe
        .main-service
            .image
                height: 400px
                img
                    height: 100%
            figcaption
                justify-content: center
                width: 370px
                left: 50%
                top: -50px
                transform: translateX(-50%)       
