.dc-home-1
    .swiper-container
        position: relative
        margin: 0 30px
        .swiper-wrapper
            .swiper-slide
                position: relative
                .image
                    img
                        width: 100%
                .title
                    position: absolute
                    top: 10px
                    width: 100%
                    h3
                        color: #fff
                        text-align: center
                        font-size: 14px
                        font-weight: 400
                        text-transform: uppercase
        .swiper-button
            display: flex
            .swiper-next
                position: absolute
                top: 47%
                right: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 24px
            .swiper-prev
                position: absolute
                top: 47%
                left: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 24px
                    
    .main-item
        padding-top: 60px
        figure
            display: flex
            flex-direction: column
            justify-content: center
            align-items: center
            margin-bottom: 20px
            .image
                margin-bottom: 10px
                img
                    width: 100%
            figcaption
                padding: 0 30px
                p
                    text-align: center
                    font-size: 14px
            
@media(min-width: 1025px)
    
    .dc-home-1.main-section
        padding-top: 0
        position: relative
        .swiper-container
            margin-top: -60px
            .swiper-wrapper
                .swiper-slide
                    position: relative
                    
                    .title
                        h3
                            font-size: 18px
                    &:nth-child(1)
                        .title
                            h3
                                font-size: 30px
                .swiper-slide.swiper-slide-active,.swiper-slide.swiper-slide-duplicate.swiper-slide-duplicate-active
                    position: relative
                    &::before
                        content: ''
                        position: absolute
                        height: 100%
                        width: 100%
                        background: #000
                        opacity: .6
                        top: 0
                        left: 0
                    .title
                        h3
                            opacity: .8
                        
            .swiper-button
                .swiper-next
                    right: 140px
                    em
                        font-size: 36px
                .swiper-prev
                    left: 140px
                    em
                        font-size: 36px
        .main-item
            figure
                margin-bottom: 0px
                
                
                   
                   
                    
                
                
