.dc-doi-tac
    padding: 30px 0
    .main-title
        margin-bottom: 20px
        @media(min-width: 768px )
            margin-bottom: 60px
    .partner
        position: relative
        .swiper-slide
            margin-bottom: 20px
            cursor: pointer
            .image
                display: flex
                justify-content: center
                align-items: center
                border: 2px solid #d7d7d7
                height: 150px
                img
                    width: 100%
                    height: 100%
                    object-fit: none
            &:hover
                box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
        .swiper-butoon
            display: flex
            .button-next1
                position: absolute
                top: 47%
                right: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 30px
            .button-prev1
                position: absolute
                top: 47%
                left: -7px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 30px
@media(min-width: 768px)
    .dc-doi-tac
        .partner
            .swiper-butoon
                .button-next1
                    right: -40px
                    em
                        font-size: 50px
                .button-prev1
                    left: -40px
                    em
                        font-size: 50px
                    
        
