.dc-thuexe-ct
    .main-content
        margin-top: 60px
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
       
    .gallery-toppp
        .swiper-container
            height: 100% 
    .gallery-toppp
        height: 100%
        .swiper-slide
            width: 100%
            
            .image
                background: #f3f3f3
                width: 100%
                height: 100%
                box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
                img
                    width: 100%
                    height: 100%
                    object-fit: contain
        &:hover
            cursor: pointer
    .gallery-thumbs
        margin-top: 20px
        padding: 0 30px
        margin-bottom: 20px
        .swiper-wrapper
            position: relative
            
            .swiper-slide
                margin-right: 0
                text-align: center
                display: flex
                justify-content: center
                align-items: center
                
                .image-thumbs
                    // padding: 0 10px
                    width: 85px
                    height: 60px
                    background: #f3f3f3
                    border: 1px solid #d7d7d7
                    img
                        width: 100%
                        height: 100%
                        top: 0
                        left: 0
                        object-fit: contain
                    &:hover
                        border: 1px solid #005cb5
                        cursor: pointer
            .swiper-slide-thumb-active
                .image-thumbs
                    border: 1px solid #005cb5
                    cursor: pointer
        .button
            position: absolute
            display: flex
            justify-content: space-between 
            align-items: center
            width: 100%
            left: 0
            top: 0                
            height: 100%
            font-size: 26px
            color: #999999
            z-index: 99
            pointer-events: none
            .left, .right
                width: 35px
                height: 35px
                font-size: 36px
                display: flex
                justify-content: center
                align-items: center
                
                pointer-events: auto
    table
        width: 100%
        
        
        tbody 
            tr 
                td 
                    font-size: 16px
                    font-weight: 400
                    color: #333333
                    padding: 5px 0
                    &.sdt
                        color: #005cb5
                        font-weight: 600
                    &:first-child
                        font-weight: 700
                        width: 180px
                        font-size: 18px
    .main-contact-phone
        padding-left: 20px
    .description
        padding: 0 10px
        .title
            padding: 10px 0
            h3
                color: #005cb5
                font-size: 18px
                text-transform: uppercase
                border-bottom: 1px solid #005cb5
                padding-bottom: 10px
        .info
            span 
                color: #333
            li
                color: #666
            p
                color: #666
                span
                    strong
                        padding: 5px 0
                        color: #333
            h4 
                color: #333333
                font-size: 16px
                padding: 5px 0
                span
                    padding-left: 5px
                    color: #333333
                    font-weight: 300
            .file,.phone
                a
                    color: #333333
                    transition: background-color 0.1s, color 0.1s
                    span,em
                        font-size: 16px
                        color: #005cb5
                    span
                        padding-left: 5px
                    &:hover
                        text-decoration: underline
                        color: #005cb5
            .phone
                padding-top: 5px
                padding-left: 10px
            .contact
                margin: 20px 0
                a
                    text-decoration: none
                    background: #005cb5
                    color: #fff
                    font-size: 16px
                    padding: 10px 40px 
                    text-transform: uppercase
                    &:hover
                        background-image: -webkit-linear-gradient( -61deg, #005cb5 100%)
                    em
                        margin-right: 5px
                        font-size: 15px
                        display: none

    .car-other
        padding-top: 60px
        .main-title
            margin-bottom: 30px
        .main-service
            .description
                p
                    padding: 20px 50px
                    font-size: 14px
                    color: #333333
                    text-align: center
                    span
                        color: #005cb5
            .image
                background: #f3f3f3
                img
                    width: 100%
                    object-fit: contain !important
            figcaption
                padding: 15px 0 0
                position: relative
                bottom: 30px
                z-index: 99
                display: flex
                flex-direction: column
                justify-content: center
                background: #fff
                transform: translateY(15px)
                width: auto
                box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
                text-align: center
                border-bottom: 2px solid #005cb5
                h3
                    text-transform: uppercase
                    font-size: 16px
                    color: #333333
                p
                    color: 14px 
                    color: #333333
                    span
                        color: #666666
                        padding-left: 10px
                .button-detail
                    display: flex
                    justify-content: center
                    align-content: center
                    width: 100%
                    margin-top: 10px
                    border-top: 1px solid #d7d7d7
                    .detail,.book-car
                        flex: 0 0 50%
                        padding: 10px 0
                        a
                            display: flex
                            justify-content: center
                            align-items: center
                            em
                                padding-right: 10px
                            p
                                color: #000000
                                font-size: 14px
                                text-transform: uppercase
                        &:hover
                            background: #005cb5
                            a
                                color: #fff
                                p,em   
                                    color: #fff
                    .detail
                        border-right: 1px solid #d7d7d7
    @media(min-width: 1025px)
        .main-content
            .col-md-6
                &:nth-child(1)
                    padding-left: 0
                    padding-right: 20px
                &:nth-child(2)
                    padding-right: 0
        .description
            padding: 30px 0 30px 10px
            .title
                h3  
                    line-height: .8
                    font-size: 24px
            .info
                h4 
                    color: #333333
                    font-size: 18px
                    span
                        color: #333333
                        font-weight: 300
                .file,.phone
                    a
                        span
                            font-size: 18px
                .phone
                    padding-left: 20px
                .contact
                    a
                        font-size: 18px
        
                
        .gallery-thumbs
            position: relative
            transform: translateX(-17%) translateY(20%)
            .swiper-wrapper
                .swiper-slide
                    .image-thumbs
                        // padding: 0 10px
                        width: 120px
                        height: 100px
        .main-service
            .image
                height: 400px
                img
                    height: 100%
            figcaption
                justify-content: center
                width: 370px
                left: 50%
                top: -50px
                transform: translateX(-50%)

.main-book
    padding-left: -10px
    padding-right: -10px
    margin-top: 60px
    background: #f7f6f0
    
    .book-service
        padding: 50px 0px 50px 80px  
        .main-title
            margin-bottom: 60px
        .image
            width: 100%
            img
                width: 100%