.dc-dulich-3
    .title
        h2
            text-transform: uppercase
            color: #005cb5
            font-size: 22px
    .main-ratting
        display: flex
        justify-content: flex-start
        align-items: center
        padding-bottom: 20px
        .star
            em
                color: #eec70a
        .ratting
            padding-left: 10px
            
            p
                color: #333333
                span
                    font-weight: 600
    .gallery-toppp
        .swiper-slide
            .image
                width: 100%
                height: auto
                img
                    width: 100%
                    height: 100%
                    object-fit: fill
        &:hover
            cursor: pointer
    .gallery-bottom
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
        padding-bottom: 30px
        .gallery-thumbs
            
            padding: 20px 30px
            .swiper-wrapper
                position: relative
                .swiper-slide
                    margin-right: 0
                    text-align: center
                    display: flex
                    justify-content: center
                    align-items: center
                    
                    .image-thumbs
                        // padding: 0 10px
                       
                        height: 60px
                        background: #f3f3f3
                        border: 1px solid #f3f3f3
                        img
                            width: 100%
                            height: 100%
                            top: 0
                            left: 0
                        &:hover
                            border: 1px solid #005cb5
                            cursor: pointer
                .swiper-slide-thumb-active
                    .image-thumbs
                        border: 1px solid #005cb5
                        cursor: pointer
            .button
                position: absolute
                display: flex
                justify-content: space-between 
                align-items: center
                width: 100%
                left: 0
                top: 0                
                height: 100%
                font-size: 26px
                color: #999999
                z-index: 99
                pointer-events: none
                .left, .right
                    width: 35px
                    height: 35px
                    font-size: 40px
                    display: flex
                    justify-content: center
                    align-items: center
                    
                    pointer-events: auto
        .button-small
            display: flex
            justify-content: center
            align-items: center
            padding: 20px 
            margin: 0 30px
            border-top: 2px solid #f3f3f3
            border-bottom: 2px solid #f3f3f3
            .book
                display: flex
                align-items: center
                width: 150px
                a
                    padding: 10px 15px
                    background: #005cb5
                    border-radius: 30px
                    color: #fff
                    font-size: 14px
                    text-transform: uppercase
                    font-weight: 400
                    width: 100%
                    text-align: center
            .hotline
                display: flex
                align-items: center
                width: 150px
                margin-left: 30px
                a
                    width: 100%
                    display: flex
                    padding: 10px 20px
                    background: #f78915
                    border-radius: 30px
                    color: #fff
                    font-size: 14px
                    text-transform: uppercase
                    font-weight: 400
                    justify-content: center
                    em
                        padding-right: 5px
    .list-tour
        padding-top: 30px
        ul
            padding: 10px 0 10px 40px
            box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
            display: flex
            overflow: auto
            white-space: nowrap
            align-items: center
            li
                list-style: none
                a
                    color: #333
                    text-transform: uppercase
                    padding: 12px 20px
                    font-size: 14px
                    font-weight: 500
                &:hover
                    a
                        background: #005cb5
                        color: #fff
    .content-tour
        padding-top: 30px
        .about-tour,.video, .tour-detail,.tour-price,.customer-image,.about-service
            h3
                color: #005cb5
                text-transform: uppercase
                border-bottom: 1px solid #005cb5
                padding: 10px 0
                margin-bottom: 10px
            p
                padding: 5px 0
        .tour-price
            overflow: auto
            table
                width: 100%
                white-space: nowrap
                tbody
                    tr
                        background: #ddeedc
                        td
                            color: #333
                            text-align: center
                            padding: 10px 30px
                            border-bottom: 1px solid #fff
                            border-right: 1px solid #fff
                            
                        &:first-child
                            background: #1c9d1e
                            td
                                color: #fff

        .swiper-slide
            figure
                background: #27aae2
                .image
                    width: 100%
                    img
                        width: 100%
                figcaption
                    padding: 20px
                    h4
                        color: #fff
                        text-transform: uppercase
                        font-size: 14px
                    .star
                        em
                            color: #eec70a
                    p
                        color: #fff
                        font-size: 14px 
                .viewmore
                    color: #fff
                    text-align: center
                    width: 100%
                    display: flex
                    justify-content: center
                    padding: 10px 0
                    border-top: 1px solid #59b7db
                    a
                        border-bottom: 1px solid #27aae2
                    &:hover
                        a
                            border-bottom: 1px solid #fff
        .customer-image
            padding-top: 30px
            .list-img
                padding-top: 30px
                figure
                    padding-top: 20
                    margin-bottom: 20px
                    .image
                        width: 100%
                        height: 150px
                        img
                            width: 100%
                            height: 100%
                            object-fit: fill
        .video
            padding-top: 30px
            iframe
                padding-top: 30px
                height: auto
                @media(min-width: 1025px)
                    height: 360px
    .list-post
        
        h2
            padding: 10px 0
            color: rgba(255, 255, 255, 255)
            text-transform: uppercase
            font-size: 16px
            background: #005cb5
            text-align: center
            margin-bottom: 20px
        .tour-other
            figure
                .image
                    img
                        width: 100%
                .title
                    position: relative
                    bottom: 30px
                    z-index: 9
                    display: flex
                    flex-direction: column
                    justify-content: center
                    background: #f5f4ed
                    transform: translateY(-30px)
                    width: 100%
                    
                    h3
                        display: flex
                        align-items: center
                        color: #000000
                        padding: 10px 20px 
                        font-size: 18px
                        justify-content: center
                        height: 50px
                        width: auto
                        text-transform: uppercase
                        text-align: center
                    .star    
                        em
                            color: #ffcc00
                            padding-left: 20px
                    .note,.address,.car
                        display: flex
                        justify-items: center
                        align-items: center
                        padding-left: 20px
                        em
                            color: #005cb5
                            margin-right: 10px
                        h6
                            color: #999999
                            font-size: 14px
                        span
                            margin-left: 5px
                            color: #333333
                            font-size: 14px
                    .car
                        margin-bottom: 10px
                    .info-price
                        padding: 10px 20px
                        display: flex
                        justify-content: space-between
                        border-top: 1px solid #d1d1d1
                        .price,.sale
                            p
                                color: #005cb5
                                font-size: 12px
                        .price
                            span
                                color: #989898
                                font-weight: 500
                                font-size: 14px
                                text-decoration: line-through
                        .sale
                            text-align: left
                            span 
                                color: #005cb5
                                font-weight: 600
                                font-size: 14px
                
                    .button-small
                        display: flex
                        justify-content: space-between
                        align-items: center
                        padding: 0 30px 20px
                        background: #f5f4ed
                        .book
                            display: flex
                            align-items: center
                            width: 150px
                            a
                                padding: 10px 15px
                                background: #005cb5
                                border-radius: 30px
                                color: #fff
                                font-size: 14px
                                text-transform: uppercase
                                font-weight: 400
                                width: 100%
                                text-align: center
                        .hotline
                            display: flex
                            align-items: center
                            width: 150px
                            
                            a
                                width: 100%
                                display: flex
                                padding: 10px 20px
                                background: #f78915
                                border-radius: 30px
                                color: #fff
                                font-size: 14px
                                text-transform: uppercase
                                font-weight: 400
                                justify-content: center
                                em
                                    padding-right: 5px
    @media(min-width: 1025px)
        .gallery-toppp
            .swiper-slide
                .image
                    width: 100%
                    height: 505px
        .list-post
            .tour-other
                padding-top: 10px
                figure
                    .title
                        justify-content: center
                        width: 370px
                        left: 50%
                        top: -50px
                        transform: translateX(-50%)
                        
                        h3
                            width: 370px
                        .star
                            em
                                padding-left: 0
                                &.fisrt
                                    padding-left: 20px
                        