.dc-tour-dulich-2
    .main-title
        margin-bottom: 20px
    .main-tour
        figure
            .image
                height: 295px
                img
                    height: 100%
                    width: 100%
            .title
                position: relative
                bottom: 30px
                z-index: 9
                display: flex
                flex-direction: column
                justify-content: flex-start
                background: #f5f4ed
                transform: translateY(-30px)
                width: 100%
                opacity: .9
                
                h3
                    display: flex
                    align-items: center
                    color: #000000
                    padding: 10px 20px 
                    font-size: 18px
                    justify-content: flex-start
                    height: 50px
                    width: auto
                    text-transform: uppercase
                    
                .star    
                    em
                        color: #ffcc00
                        padding-left: 20px
                .note,.address,.car
                    display: flex
                    justify-items: center
                    align-items: center
                    padding-left: 20px
                    em
                        color: #005cb5
                        margin-right: 10px
                    h5
                        color: #999999
                        font-size: 14px
                        font-weight: 500
                    span
                        margin-left: 5px
                        color: #333333
                        font-size: 14px
                .car
                    margin-bottom: 10px
                .info-price
                    padding: 10px 20px
                    display: flex
                    justify-content: space-between
                    border-top: 1px solid #d1d1d1
                    .price,.sale
                        p
                            color: #005cb5
                            font-size: 12px
                    .price
                        span
                            color: #989898
                            font-weight: 500
                            font-size: 14px
                            text-decoration: line-through
                    .sale
                        text-align: left
                        span 
                            color: #005cb5
                            font-weight: 600
                            font-size: 14px
            
                .button-small
                    display: flex
                    justify-content: space-between
                    align-items: center
                    padding: 0 30px 20px
                    background: #f5f4ed
                    .book
                        display: flex
                        align-items: center
                        width: 150px
                        a
                            padding: 10px 15px
                            background: #005cb5
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            width: 100%
                            text-align: center
                    .hotline
                        display: flex
                        align-items: center
                        width: 150px
                        
                        a
                            width: 100%
                            display: flex
                            padding: 10px 20px
                            background: #f78915
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            justify-content: center
                            em
                                padding-right: 5px
            &:hover
                .image
                    transform: scale(1.1)
    .main-form
        
        display: none
        justify-content: center
        align-items: center
        z-index: 199
        position: fixed
        top: 10%
        width: 100%
        left: 50%
        transform: translateX(-50%)
        
        .book-form
            padding: 40px 70px
            width: 50%
            background: #fff
        &.active
            display: flex
    .backdrop
        position: fixed
        left: 0
        top: 0
        opacity: 0
        pointer-events: none
        width: 100%
        height: 100vh
        background: rgba(0,0,0,.7)
        z-index: 198
        &.active
            opacity: 1
            pointer-events: initial


        .wrap
@media(min-width: 1025px)
    .dc-tour-dulich-2
        .main-tour
            padding-top: 50px
            figure
                .title
                    justify-content: center
                    width: 370px
                    left: 50%
                    top: -50px
                    transform: translateX(-50%)
                    
                    h3
                        width: 370px
                    .star
                        em
                            padding-left: 0
                            &.fisrt
                                padding-left: 20px
                    