.dc-lien-he
    padding: 30px 0
    .main-title
        margin-bottom: 60px
    .main-content
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
        padding: 30px
        .local
            padding: 30px 20px
            background: #fff
            margin-bottom: 20px
            cursor: pointer
            position: relative
            &:before
                content: ''
                width: 100%
                height: 1px
                position: absolute
                background: #e1e1e1
                bottom: -12px
                left: 0
            
            &:last-child
                margin-bottom: 0
                &:before
                    display: none
            h3
                color: #005cb5
                font-size: 16px
                text-transform: uppercase
                font-weight: 600
            li
                list-style: none
                display: flex
                padding: 5px 0
                em
                    padding-right: 10px
                    color: #999999
                    font-size: 16px
                h4
                    color: #333333
                    font-size: 14px   
                    font-weight: 400
          

    .main-maps
        margin-bottom: 10px
        iframe
            height: 300px
            width: 100%
            border: none
    .main-form
        margin-top: 40px
        background: #f3f3f3
        text-align: center
        padding: 10px 20px
        .desc-form
            h4
                font-size: 16px
                color: #333333
            span
                font-size: 16px
                color: #005cb5
                font-weight: 600
        
    .wrap-form
        margin-bottom: 20px
        .form-group
            label 
                display: none
    .map
        padding-top: 60px
    @media(max-width: 767px)
        .main-form
            .form-group.frm-btnwrap
                .frm-btn
                    justify-content: center
        .wrap-form
            margin-bottom: 20px
            .frm-captcha
                display: block
                .RadCaptcha.RadCaptcha_Default
                    padding-top: 20px
    @media(min-width: 768px)
        .main-maps
            height: 100%
            iframe
                height: 100%

    @media(min-width: 1025px)
        .main-form
            height: 640px
            .desc-form
                padding: 25px 100px
                margin-bottom: 0
                h4 
                    font-size: 16px
                    font-weight: 400
                    color: #333333
        .wrap-form>div
            
            .form-group
                padding-left: 15px
                padding-right: 15px
                input, textarea
                    &::placeholder
                        font-style: italic
                        color: #999999
                        font-size: 14px
            textarea
                height: 270px
        