.dc-dichvu-ct
    .main-content
        margin-top: 60px
        box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
        padding-bottom: 20px
            
    .gallery-toppp
        .swiper-slide
            .image
                width: 100%
                height: 365px
                img
                    width: 100%
                    height: 100%
                    object-fit: fill
        &:hover
            cursor: pointer
    .gallery-thumbs
        margin-top: 20px
        padding: 0 30px
        .swiper-wrapper
            position: relative
            
            .swiper-slide
                margin-right: 0
                text-align: center
                display: flex
                justify-content: center
                align-items: center
                width: 100px
                .image-thumbs
                    // padding: 0 10px
                    width: 110px
                    height: 60px
                    background: #f3f3f3
                    border: 1px solid #f3f3f3
                    img
                        width: 100%
                        height: 100%
                        top: 0
                        left: 0
                    &:hover
                        border: 1px solid #005cb5
                        cursor: pointer
            .swiper-slide-thumb-active
                .image-thumbs
                    border: 1px solid #005cb5
                    cursor: pointer
        .button
            position: absolute
            display: flex
            justify-content: space-between 
            align-items: center
            width: 100%
            left: 0
            top: 0                
            height: 100%
            font-size: 26px
            color: #999999
            z-index: 99
            pointer-events: none
            .left, .right
                width: 35px
                height: 35px
                font-size: 36px
                display: flex
                justify-content: center
                align-items: center
                
                pointer-events: auto
                em
                    color: #b5b5b5
    table
        width: 100%
        border-bottom: 1px solid #e5e5e5
        
        tbody 
            tr 
                td 
                    font-size: 16px
                    font-weight: 400
                    color: #333333
                    padding: 5px 0
                    &.sdt
                        color: #005cb5
                        font-weight: 600
                    &:first-child
                        font-weight: 700
                        width: 180px
                        font-size: 18px
    .main-contact-phone
        padding-left: 20px
    .description
        padding: 0 10px
        .title
            padding: 10px 0
            h3
                color: #005cb5
                font-size: 18px
                text-transform: uppercase
                border-bottom: 1px solid #005cb5
                padding-bottom: 10px
        .info
            h4 
                color: #333333
                font-size: 16px
                padding: 5px 0
                span
                    padding-left: 5px
                    color: #333333
                    font-weight: 300
            .file,.phone
                a
                    color: #333333
                    transition: background-color 0.1s, color 0.1s
                    span,em
                        font-size: 16px
                        color: #005cb5
                    span
                        padding-left: 5px
                    &:hover
                        text-decoration: underline
                        color: #005cb5
            .phone
                padding-top: 5px
                padding-left: 10px
            .contact
                margin: 20px 0
                a
                    text-decoration: none
                    background: #005cb5
                    color: #fff
                    font-size: 16px
                    padding: 10px 40px 
                    text-transform: uppercase
                    &:hover
                        background-image: -webkit-linear-gradient( -61deg, #005cb5 100%)
                    em
                        margin-right: 5px
                        font-size: 15px
                        display: none
        .button-small
            display: flex
            
            align-items: center
            padding: 20px 0
            border-bottom: 1px solid #e5e5e5
            .book
                display: flex
                align-items: center
                width: 120px
                margin-right: 20px
                a
                    padding: 10px 15px
                    background: #005cb5
                    border-radius: 30px
                    color: #fff
                    font-size: 14px
                    text-transform: uppercase
                    font-weight: 400
                    width: 100%
                    text-align: center
            .hotline
                display: flex
                align-items: center
                width: 120px
                a
                    width: 100%
                    display: flex
                    padding: 10px 20px
                    background: #f78915
                    border-radius: 30px
                    color: #fff
                    font-size: 14px
                    text-transform: uppercase
                    font-weight: 400
                    em
                        padding-right: 5px
    .tab-news-ct
        padding-top: 50px
        ul
            padding-left: 0
            display: flex
            align-items: center
            justify-content: flex-start
            overflow: auto
            white-space: nowrap
            
            li
                width: 250px
                margin-right: 20px
                list-style: none
                padding: 10px 8px
                border-top: 1px solid #005cb5
                border-left: 1px solid #005cb5
                border-right: 1px solid #005cb5
                text-transform: uppercase
                text-align: center
                border-radius: none !important
                a
                    color: #005cb5
                    font-weight: 600
                    font-size: 14px
                &:hover,&.active
                    background: #005cb5
                    a
                        color: #fff
    .schedule
        .tab-content
            display: none
            cursor: pointer
            &.active
                display: block
        .main-content-schedule
            background: #f0f4f7
            padding: 30px
            img
                margin: 20px 0
            .content
                p
                    color: #333333
                    font-size: 16px
            .schedule-detail
                padding: 30px 0
                h4
                    color: #005cb5
                    text-transform: uppercase
                    font-size: 16px
                    border-bottom: 1px solid #005cb5
                    padding-bottom: 10px
    @media(min-width: 1025px)
        .description
            padding: 30px
            .title
                h3
                    line-height: .8
                    font-size: 24px
            .info
                h4 
                    color: #333333
                    font-size: 18px
                    span
                        color: #333333
                        font-weight: 300
                .file,.phone
                    a
                        span
                            font-size: 18px
                .phone
                    padding-left: 20px
                .contact
                    a
                        font-size: 18px
        .tab-news
            ul
            
                padding-bottom: 0