.dc-tour-dulich
    padding-top: 0
    .main-title
        margin-bottom: 30px
    .list-project
        padding-top: 20px
        
        .list-top
            display: flex
            .item
                margin-right: 5px
                a
                    .image
                        width: 175px
                        height: 135px
                        img
                            height: 135px
                            width: 100%
                    .title-post
                        display: none    
                &:nth-child(2)
                    margin-right: 0
        .list-center
            margin: 5px 0
            margin-bottom: 40px
            .item
                height: 135px
                a
                    .image
                        position: relative
                        height: 135px
                        width: 100%
                        
                        img
                            width: 100%
                            object-fit: cover
                            height: inherit
                    .title-post
                        display: none   
        .list-bottom
            display: flex
            .item
                margin-right: 5px
                &:nth-child(2)
                    margin-right: 0
                a
                    .image
                        width: 100%
                        height: 135px
                        position: relative
                        overflow: hidden
                        img
                            width: 100%
                            height: 135px
                    .title-post
                        display: none
        .list-top,.list-center,.list-bottom
            .item
                a
                    figcaption
                        position: relative
                        bottom: 30px
                        z-index: 99
                        display: flex
                        justify-content: center
                        
                        .title
                            background: #ffffff
                            border-bottom: 2px solid #005cb5
                            h3
                                display: flex
                                align-items: center
                                color: #000000
                                padding: 10px 20px 
                                font-size: 14px
                                justify-content: center
                                height: 50px
                                width: auto
                                text-transform: uppercase
                                text-align: center
                            
                                
                            p
                                text-align: center
                                color: #333333
                                &.price
                                    
                                    padding-bottom: 10px
                                    font-weight: 500
                                span
                                    padding-left: 5px
                                    color: #666666 
                                    font-size: 14px
                        &:hover
                            .title
                                background: #005cb5
                                h3,p,span
                                    color: #fff  
                
                            
    @media(min-width: 768px)
        .list-project
            .list-top
                padding-right: 15px
                display: inline
                .item
                    margin-right: 0
                    margin-bottom: 30px
                    height: 300px
                    a
                        .image
                            height: 100% 
                            width: 100% !important
                            position: relative
                            img
                                height: 100%
                                width: 100% !important
                        .title-post
                            position: absolute
                            z-index: 99
                            top: 80%
                            text-transform: uppercase
                            left: 50%
                            transform: translateX(-50%)
                            width: 100%
                            text-align: center
                            h3 
                                font-size: 18px
                                color: #fff
                        &:hover
                            .title-post
                                top: 50%
                                transform: translateY(-50%) translateX(-50%)
            .list-center
                padding: 0 15px
                margin: 0
                .item
                    height: 630px
                    a
                        .image
                            height: 100%
                            position: relative
                            overflow: none
                            img
                                height: 100%
                        .title-post
                            position: absolute
                            z-index: 99
                            top: 80%
                            text-transform: uppercase
                            left: 50%
                            transform: translateX(-50%)
                            width: 100%
                            text-align: center
                            h3 
                                font-size: 18px
                                color: #fff
                        &:hover
                            .title-post
                                top: 50%
                                transform: translateY(-50%) translateX(-50%)
            .list-bottom
                padding-left: 15px
                display: inline
                .item
                    margin-right: 0
                    margin-bottom: 30px
                    height: 300px
                    a
                        .image
                            height: 100%
                            position: relative
                            img
                                height: 100%
                        .title-post
                            position: absolute
                            z-index: 99
                            top: 80%
                            text-transform: uppercase
                            left: 50%
                            transform: translateX(-50%)
                            width: 100%
                            text-align: center
                            h3 
                                font-size: 18px
                                color: #fff
                        &:hover
                            .title-post
                                top: 50%
                                transform: translateY(-50%) translateX(-50%)    
    @media(min-width: 1025px)
        .list-project
            margin-bottom: 0
                    
            .list-top, .list-center, .list-bottom
                .item
                    a
                        .title-post
                            display: inline
                        figcaption
                            .title
                                h3
                                    width: 370px
    
            
