.dc-home-2
    background: #f7f6f0
    .main-content
        margin-top: 35px
        .item
            .content
                margin-bottom: 20px
                figure
                    background: #fff
                    padding-bottom: 34px
                    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
                    transition: background-color 0.1s, color 0.1s
                    .image
                        height: 440px
                        img
                            height: 100%
                            width: 100%
                    figcaption
                        padding: 10px 40px 20px
                        h3
                            margin-top: 10px
                            margin-bottom: 20px
                            color: #333333
                            font-size: 16px
                            font-weight: 600
                            text-transform: uppercase
                        p
                            color: #333333
                            font-size: 14px
                        @media(min-width: 1025px)
                            h3
                                font-size: 30px
                    .button
                        display: flex
                        justify-content: center
                        align-items: center
                        border: 2px solid #005cb5
                        border-radius: 30px
                        width: 180px
                        margin-left: 40px
                        transition: background-color 0.1s, color 0.1s
                        a
                            color: #005cb5
                            padding: 10px 20px
                        em
                            color: #005cb5
                            font-size: 26px
                            
                    &:hover
                        background: #005cb5
                        figcaption
                            h3
                                color: #fff
                            p
                                color: #ffffff
                        .button
                            border: 2px solid #fff
                            a,em
                                color: #fff
        .small-item
            figure
                position: relative
                padding-bottom: 20px
                background: #fff
                margin-bottom: 15px
                box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.08)
                .image
                    height: 200px
                    img
                        height: 100%
                        width: 100%
                figcaption
                    padding: 15px 20px 5px
                    background: #fff
                    opacity: .9
                    margin-top: -70px
                    h4
                        color: #000
                        font-size: 16px
                        height: 50px
                        margin-bottom: 20px
                    @media(min-width: 1025px)
                        h4
                            font-size: 18px
                    em
                        color: #ffcc00
                    .info-price
                        display: flex
                        justify-content: space-between
                        .price,.sale
                            p
                                color: #989898
                                font-size: 12px
                        .price
                            span
                                color: #989898
                                font-weight: 500
                                font-size: 14px
                                text-decoration: line-through
                        .sale
                            text-align: left
                            span 
                                color: #2b3990
                                font-weight: 600
                                font-size: 14px
                
                .button-small
                    display: flex
                    justify-content: space-between
                    align-items: center
                    padding: 0 20px
                    
                    .book
                        display: flex
                        align-items: center
                        width: 120px
                        a
                            padding: 10px 15px
                            background: #005cb5
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            width: 100%
                            text-align: center
                    .hotline
                        display: flex
                        align-items: center
                        width: 120px
                        a
                            width: 100%
                            display: flex
                            padding: 10px 20px
                            background: #f78915
                            border-radius: 30px
                            color: #fff
                            font-size: 14px
                            text-transform: uppercase
                            font-weight: 400
                            em
                                padding-right: 5px

        .view-more
            display: flex
            justify-content: center
            align-items: center
            width: 100%
            transition: background-color 0.1s, color 0.1s
            a
                display: flex
                justify-content: center
                align-items: center
                color: #005cb5
                padding: 5px 10px
                border: 1px solid #005cb5
                border-radius: 30px
                text-transform: uppercase
                font-weight: 500
                width: 250px
                font-size: 14px
                em
                    color: #005cb5
                    font-size: 26px
                    margin-left: 20px    
                &:hover
                    background: #005cb5
                    color: #fff
                    border: 1px solid #fff
                    em
                        color: #fff
                
    @media(min-width: 1025px)
        .main-content
            .item
                margin-bottom: 20px
                .content
                    margin-bottom: 0
                    figure
                        .image
                            height: 440px
                            img
                                height: 100%
                &:nth-child(2n)
                    .col-lg-6
                        &:first-child
                            order: 2
                            &::before
                                right: 10px
                                left: initial
                        &:last-child
                            order: 1
            .small-item
                figure
                    .image
                        height: 200px
                        img
                            height: 100%
                            width: 100%


                    


                            
        