.dc-dulich.main-section
    .news
        padding-top: 30px
        figure
            margin-bottom: 20px
            a
                display: flex
                background: #fff
                box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
                .image
                    flex: 0 0 50%
                    height: 220px
                    width: 200px
                    img
                        height: 100%
                        width: 100%
                figcaption
                    flex: 0 0 50%
                    padding: 20px 20px
                    span
                        color: #989898
                        font-size: 12px
                    h4
                        color: #333333
                        font-size: 14px
                        font-weight: 500
                        margin-bottom: 5px
                        overflow: hidden
                        text-overflow: ellipsis
                        line-height: 25px
                        -webkit-line-clamp: 2
                        height: 50px
                        display: -webkit-box
                        -webkit-box-orient: vertical
                    p
                        color: #666666
                        font-size: 12px
                        overflow: hidden
                        text-overflow: ellipsis
                        line-height: 20px
                        -webkit-line-clamp: 4
                        height: 80px
                        display: -webkit-box
                        -webkit-box-orient: vertical
                    
                        
                &:hover
                    figcaption
                        h4
                            text-decoration: underline
        &:last-child
            padding: 0 
        
