@media(min-width: 1025px)
    .dc-dulich.main-section
        .news
            padding-top: 60px
            .col-md-8
                figure
                    height: 100%
                    a
                        
                        .image
                            height: 460px
                            img
                                height: 100%
                                object-fit: fill
                        figcaption
                            padding: 30px
                            h4
                                font-size: 24px
                            p
                                font-size: 18px
                                color: #666666
                                overflow: hidden
                                text-overflow: ellipsis
                                line-height: 25px
                                -webkit-line-clamp: 8
                                height: 200px
                                display: -webkit-box
                                -webkit-box-orient: vertical
            &:last-child
                padding-top: 30px 
            .col-lg-4
                .item
                    &:last-child
                        figure
                            margin-bottom: 0
                            a
                                background: #31b9d1
                                .image
                                    order: 2
                                figcaption
                                    order: 1
                                    span,h4,p
                                        color: #fff
            .col-md-6.col-lg-4
                &:nth-child(1)
                    figure
                        a
                            background: #31b9d1
                            figcaption
                                span,h4,p
                                    color: #fff
            .col-md-6.col-lg-4
                &:nth-child(3)
                    figure
                        a
                            background: #34ba9c
                            figcaption
                                span,h4,p
                                    color: #fff            
            .col-md-6.col-lg-4
                &:nth-child(4)
                    figure
                        a
                            background: #34ba9c
                            .image
                                order: 2
                            figcaption
                                order: 1
                                span,h4,p
                                    color: #fff
            .col-md-6.col-lg-4
                &:nth-child(5)
                    figure
                        a
                            .image
                                order: 2
                            figcaption
                                order: 1
            .col-md-6.col-lg-4
                &:nth-child(6)
                    figure
                        a
                            background: #31b9d1
                            .image
                                order: 2
                            figcaption
                                order: 1
                                span,h4,p
                                    color: #fff
