.dc-tuyendung
    padding: 30px 0
    .main-content
        padding-top: 50px
        .item
            padding-bottom: 40px
            .content
                padding-bottom: 10px
                margin: 0 !important
                h3
                    color: #005cb5
                    font-size: 18px
                    font-weight: 700
                    text-transform: capitalize
                    line-height: 1
                    margin-bottom: 20px
                    border-bottom: 1px solid #005cb5
                    text-transform: uppercase
                    line-height: 1.2
                    padding-bottom: 10px
                p
                    color: #333333
                    font-size: 16px
            .briefcontent
                font-size: 16px
                color: #333333
            .image
                min-height: 400px
                width: 100%
                height: 100%
                position: relative
                overflow: hidden
                margin-left: 0 !important
                img
                    width: 100%
                    position: absolute
                    height: 100%
                    
                    min-height: 400px
            ul 
                padding-left: 20px
                li 
                    list-style-type: none
                    position: relative
                    &:before
                        font-family: 'Material Design Icons'
                        content: '\f764'
                        position: absolute
                        top: 6px
                        left: -20px
                        color: #999999
                        font-size: 12px
    @media(min-width: 768px)
        .main-content
            .item
                .content
                    padding: 30px
                    h3 
                        font-size: 25px
                .briefcontent
                    font-size: 18px
            .col-md-6
                position: relative
                &:before
                    content: ''
                    position: absolute
                    top: 0
                    left: 10px
                    background: #ffffff
                    width: calc(100% + 30px)
                    height: 100%
                    box-shadow: 0 0 20px 0px rgba(0,0,0,0.1)
                    z-index: -1
                    pointer-events: none
                &:last-child
                    &::before
                        display: none
    @media(min-width: 1025px)
        .main-content
            .item
                .content
                    padding: 50px 30px
                    h3 
                        font-size: 24px 
                .briefcontent
                    font-size: 16px
                &:nth-child(1)
                    .col-md-6
                        &:first-child
                            order: 2
                            &::before
                                right: 10px
                                left: initial
                        &:last-child
                            order: 1