footer
    background: #005cb5
    position: relative
    .footer-top
        padding: 40px 0 20px
        .logo-footer
            width: 100%
            display: flex
            justify-content: center
            .image
                width: 250px
                img
                    width: 100%
        .footer-contact
            h2
                color: #fff
                font-size: 14px
                font-weight: 600
                text-transform: uppercase
                margin-bottom: 3px
                margin-top: 25px
            p
                color: #bde0fc
                font-size: 14px
                font-weight: 400
                margin-bottom: 3px
        .tour-center,.link
            h3
                color: #fff
                font-size: 16px
                text-transform: uppercase
                font-weight: 600
                padding-top: 17px
            ul
                padding-left: 0
                padding-top: 0
                li
                    list-style: none
                    a
                        padding-left: 5px
                        color: #bde0fc
                        font-size: 14px
                    em 
                        color: #bde0fc
                    &:hover,.active
                        a
                            color: #fff
        iframe
            margin-top: 20px
        #gotop
            display: inline
            padding: 5px 10px
            background: #27aae1
            color: #fff
            position: absolute
            bottom: 70%
            right: 5%
            cursor: pointer
            font-size: 24px
            z-index: 990
    .coppyright
        display: inline
        width: 100%
        justify-content: space-between
        padding-top: 20px
        border-top: 1px solid #2674c0
        margin-top: 40px
        .text
            padding-left: 20px
            h5
                font-size: 14px
                color: #bde0fc
                font-weight: 400
                text-align: center
        ul
            padding-left: 0
            display: flex
            justify-content: center
            align-items: center
            
            li
                list-style: none
                line-height: 1
                a
                    color: #bde0fc
                    font-size: 13px
                    position: relative
                    padding: 0 5px
                    line-height: 1
                    &:hover
                        color: #fff
                    &:before
                        content: ''
                        position: absolute
                        width: 1px
                        height: 100%
                        top: 0
                        left: 0
                        background: #bde0fc
                &:first-child
                    a
                        padding-left: 0
                        &:before
                            display: none
                &:last-child
                    a 
                        padding-right: 0
    @media(min-width: 768px)
        .footer-top
            .logo-footer
                justify-content: flex-start
                .image
                    width: 270px
    @media(min-width: 1025px)
        .footer-top
            .tour-center,.link
                ul
                    padding-top: 40px
        .coppyright
            display: flex
            .text
                h5
                    text-align: left
            
                    
