.dc-home-4.main-section
    // background: url('../img/banner/3.png')
   
    background-size: cover
    
    .container
        position: relative
        .swiper-container
            .swiper-slide
                .image
                    height: 245px
                    img
                        height: 100%
                        width: 100%
                a   
                    position: relative
                    bottom: 30px
                    z-index: 99
                    display: flex
                    justify-content: center
                    
                    .title
                        h3
                            display: flex
                            align-items: center
                            color: #000000
                            background: #ffffff
                            padding: 10px 20px 
                            font-size: 14px
                            justify-content: center
                            height: 50px
                            width: auto
                            text-transform: uppercase
                            text-align: center
                            border-bottom: 2px solid #005cb5
                    &:hover
                        .title
                            h3
                                background: #005cb5
                                color: #fff
        .swiper-button
            
            display: flex
            .swiper-next1
                position: absolute
                top: 40%
                right: 0px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 50px
            .swiper-prev1
                position: absolute
                top: 40%
                left: 0px
                transform: translateY(-50%)
                width: 35px
                height: 35px
                
                display: flex
                justify-content: center
                align-items: center
                z-index: 99
                &:focus
                    outline: none
                em
                    color: #b5b5b5
                    font-size: 50px
        .list-news
            .news
                padding-top: 60px
                figure
                    margin-bottom: 20px
                    a
                        display: flex
                        background: #fff
                        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
                        .image
                            flex: 0 0 50%
                            width: 200px
                            height: 220px
                            img
                                height: 100%
                                width: 100%
                        figcaption
                            flex: 0 0 50%
                            padding: 10px 20px
                            span
                                color: #989898
                                font-size: 12px
                            h4
                                color: #333333
                                font-size: 14px
                                font-weight: 500
                                margin-bottom: 5px
                                overflow: hidden
                                text-overflow: ellipsis
                                line-height: 25px
                                -webkit-line-clamp: 2
                                height: 50px
                                display: -webkit-box
                                -webkit-box-orient: vertical
                            p
                                color: #666666
                                font-size: 12px
                                font-size: 12px
                            .content
                                p
                                    color: #666666
                                    overflow: hidden
                                    text-overflow: ellipsis
                                    line-height: 15px
                                    -webkit-line-clamp: 5
                                    height: 72px
                                    display: -webkit-box
                                    -webkit-box-orient: vertical
                        &:hover
                            figcaption
                                h4
                                    text-decoration: underline
                    
                
                // &:nth-child(3)
                //     padding-top: 0
                //     .col-md-6.col-lg-4
                //         &:nth-child(1)
                //             figure
                //                 a
                //                     background: #31b9d1
                //                     figcaption
                //                         span,h4,p
                //                             color: #fff
                //         &:nth-child(3)
                //             figure
                //                 a
                //                     background: #31b9d1
                //                     figcaption
                //                         span,h4,p
                //                             color: #fff
    .view-more
        display: flex
        justify-content: center
        align-items: center
        width: 100%
        transition: background-color 0.1s, color 0.1s
        padding-top: 20px
        padding-bottom: 60px
        a
            display: flex
            background: #005cb5
            justify-content: center
            align-items: center
            color: #fff
            padding: 5px 10px
            
            border-radius: 30px
            text-transform: uppercase
            font-weight: 500
            width: 200px
            font-size: 14px
            em
                color: #fff
                font-size: 26px
                margin-left: 20px    
            &:hover
                background: #fff
                color: #005cb5
                em
                    color: #005cb5
.custom-dc-home-4
    background: url('/Data/Sites/1/media/banner/3.png')           
    padding: 30px 0
@media(min-width: 1025px)
    .custom-dc-home-4
        padding: 60px 0
    .dc-home-4.main-section
        padding: 0
        .container
            .swiper-container
                margin: 0 85px
                transform: translateY(-60px)
                .swiper-slide
                    a
                        .title
                            h3
                                width: 215px
            .swiper-button
                .swiper-next1
                    top: 20%
                    right: 5%
                    em
                        font-size: 50px
                .swiper-prev1
                    top: 20%
                    left: 5%
                    em
                        font-size: 50px
        .list-news
            .news
                .col-md-6.col-lg-4
                    &:nth-child(2)
                        figure
                            a
                                background: #34ba9c
                                figcaption
                                    span,h4,p
                                        color: #fff
                .col-md-6.col-lg-4
                    &:nth-child(4)
                        figure
                            a
                                background: #31b9d1
                                .image
                                    order: 2
                                figcaption
                                    order: 1
                                    span,h4,p
                                        color: #fff
                .col-md-6.col-lg-4
                    &:nth-child(5)
                        figure
                            a
                                .image
                                    order: 2
                                figcaption
                                    order: 1
                .col-md-6.col-lg-4
                    &:nth-child(6)
                        figure
                            a
                                background: #31b9d1
                                .image
                                    order: 2
                                figcaption
                                    order: 1
                                    span,h4,p
                                        color: #fff
