.dc-gioi-thieu-1
    background: #fffefa
    .main-title
        margin-bottom: 20px
        @media(min-width: 768px)
            margin-bottom: 60px
    .gallery-toppp
        position: relative
        height: 100%
        z-index: 99
        .swiper-slide
            width: 100%
            
            .image
                background: #f3f3f3
                width: 100%
                height: 355px
                box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1)
                img
                    width: 100%
                    height: 100%
                    object-fit: fill
        &:hover
            cursor: pointer
    .title
        h3
            color: #333333
            font-size: 24px
            padding-top: 20px
            @media(min-width: 768px)
                padding-top: 0
    .info
        padding-top: 30px 
        
    .gallery-thumbs
        position: relative
        margin-top: 20px
        padding: 0 30px
        margin-bottom: 20px
        z-index: 100
        .swiper-wrapper
            position: relative
            
            .swiper-slide
                margin-right: 0
                text-align: center
                display: flex
                justify-content: center
                align-items: center
                
                .image-thumbs
                    // padding: 0 10px
                    width: 85px
                    height: 60px
                    background: #f3f3f3
                    border: 1px solid #f3f3f3
                    img
                        width: 100%
                        height: 100%
                        top: 0
                        left: 0
                    &:hover
                        border: 1px solid #005cb5
                        cursor: pointer
            .swiper-slide-thumb-active
                .image-thumbs
                    border: 1px solid #005cb5
                    cursor: pointer
        .button
            position: absolute
            display: flex
            justify-content: space-between 
            align-items: center
            width: 101%
            left: 0
            top: 0                
            height: 100%
            font-size: 26px
            color: #999999
            z-index: 99
            pointer-events: none
            .left, .right
                width: 35px
                height: 35px
                font-size: 40px
                display: flex
                justify-content: center
                align-items: center
                
                pointer-events: auto
                em
                    font-size: 36px
            
    .main-item
        padding-top: 120px
        figure
            display: flex
            flex-direction: column
            justify-content: center
            align-items: center
            margin-bottom: 20px
            .image
                margin-bottom: 10px
                img
                    width: 100%
            figcaption
                padding: 0 30px
                p
                    text-align: center
                    font-size: 14px
    @media(min-width: 1025px)
        padding-top: 10px
        padding-bottom: 120px
        .gallery-toppp
            height: 100%
            z-index: 9
            .swiper-slide
                width: 100%
                
                .image
                    background: #f3f3f3
                    width: 100%
                    height: 510px
                    
                    img
                        width: 100%
                        height: 100%
        .box
            position: relative
            margin-top: 50px
            &:before
                content: ''
                width: 120%
                height: 110%
                right: 0
                border: 4px solid #005cb5
                position: absolute
                top: 0
                
                z-index: 8
            .description
                .title
                    top: -25px
                    left: -10px
                    background: #fff
                    position: absolute
                    z-index: 9
                    padding: 10px 50px 10px 20px
                
                .gallery-thumbs
                    position: relative
                    transform: translateX(-17%) translateY(50%)
                    z-index: 9
                    .swiper-wrapper
                        .swiper-slide
                            .image-thumbs
                                // padding: 0 10px
                                width: 120px
                                height: 100px
        .main-item
            figure
                margin-bottom: 0px
        .info
            p
                padding-right: 50px
    
.dc-content
    .main-content
        padding-top: 30px
        .item
            padding-bottom: 40px
            .content
                padding-bottom: 10px
                margin: 0 !important
                h3
                    
                    color: #333
                    font-size: 22px
                    font-weight: 700
                    text-transform: capitalize
                    line-height: 1
                    margin-bottom: 20px
                p
                    padding-bottom: 30px
                    color: #333333
                    font-size: 16px
            .briefcontent
                font-size: 16px
                color: #333333
            .image
                min-height: 400px
                width: 100%
                height: 100%
                position: relative
                overflow: hidden
                margin-left: 0 !important
                img
                    width: 100%
                    position: absolute
                    height: 100%
                    
                    min-height: 400px
            ul 
                padding-left: 20px
                li 
                    list-style-type: none
                    position: relative
                    &:before
                        font-family: 'Material Design Icons'
                        content: '\f764'
                        position: absolute
                        top: 6px
                        left: -20px
                        color: #999999
                        font-size: 12px
        &:first-child
            background: #f7f6f0
        &:last-child
            margin: 0
            background: #fffefa
            .item
                .content
                    h3
                        padding-top: 20px
    @media(min-width: 768px)
        .main-content
            .item
                .content
                    padding: 30px
                    h3 
                        font-size: 25px
                .briefcontent
                    font-size: 18px
            .col-md-6
                position: relative
                &:before
                    content: ''
                    position: absolute
                    top: 0
                    left: 10px
                    background: #ffffff
                    width: calc(100% + 30px)
                    height: 100%
                    
                    z-index: -1
                    pointer-events: none
                &:last-child
                    &::before
                        display: none
    @media(min-width: 1025px)
        .main-content
            .container
                .item
                    .image
                        transform: translateY(-15%)
                    .content
                        padding: 50px 30px
                        h3 
                            font-size: 24px
                    .briefcontent
                        font-size: 16px
                    &:nth-child(2n)
                        .col-md-6
                            &:first-child
                                order: 2
                                &::before
                                    right: 10px
                                    left: initial
                            &:last-child
                                order: 1